
.teacher {
  padding: 15px 10px;
}
.teacher ul li {
  height: 70px;
  background: #ebebeb;
  border-radius: 4px;
  padding: 2px 8px;
  margin: 7px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.teacher ul li img {
  height: 40px;
  width: 40px;
}
.teacher ul li .t-info {
  width: 79%;
}
.teacher ul li .t-name {
  color: #cc1138;
  font-size: 14px;
  padding: 3px 15px;
}
.teacher ul li .t-title {
  color: #858585;
  font-size: 12px;
  padding: 3px 0 3px 15px;
  width: 88%;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.teacher ul li .right {
  height: 15px;
  width: 9.2px;
  margin: 0 10px 0 0;
}


