
@import "../static/css/reset.css";
#app {
  background: #fff;
  position: relative;
}
#app .homeNav {
  height: 60px;
  width: 100%;
  background: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 1000;
}
#app .homeNav .h-menu {
  height: 100%;
  border-top: solid 1px rgba(200,198,196,0.8);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-item-align: center;
      align-self: center;
}
#app .homeNav .h-menu a {
  width: 25%;
/*border-right solid 1px #e3e2e1*/
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  font-size: 12px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 8px 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  color: #333;
}
#app .homeNav .h-menu a .iconfont {
  font-size: 20px;
}
#app .homeNav .h-menu a img {
  height: 20px;
  width: auto;
}
#app .homeNav .h-menu .active {
  color: #cc1138;
}


