
.teacherDetail {
  padding: 10px 0 60px 0;
}
.teacherDetail .detailBox {
  padding: 0 15px;
  border-bottom: solid 4px rgba(235,235,235,0.9);
}
.teacherDetail .detailBox .detail {
  height: 70px;
  border-radius: 4px;
  padding: 2px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.teacherDetail .detailBox .detail img {
  height: 40px;
  width: 40px;
}
.teacherDetail .detailBox .detail .t-info {
  width: 79%;
}
.teacherDetail .detailBox .detail .t-name {
  color: #cc1138;
  font-size: 14px;
  padding: 3px 0;
}
.teacherDetail .detailBox .detail .t-title {
  color: #858585;
  font-size: 12px;
  padding: 3px 0 3px 0;
  width: 100%;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.teacherDetail .detailBox .t-content {
  padding: 15px 0 15px 0;
}
.teacherDetail .detailBox .t-content .t-title {
  font-size: 14px;
  color: #cc1138;
  padding-bottom: 5px;
}
.teacherDetail .detailBox .t-content .t-Introduction {
  font-size: 12px;
  line-height: 1.5;
  color: #333;
}
.teacherDetail .detailBox .t-content .t-course {
  margin: 10px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.teacherDetail .detailBox .t-content .t-course li {
  width: 48%;
}
.teacherDetail .detailBox .t-content .t-course li img {
  height: 100px;
}
.teacherDetail .detailBox .t-content .t-course li span {
  font-size: 12px;
  color: #000;
  display: inline-block;
  padding: 7px 0 0 0;
}


