
.activity {
  padding: 5px 15px 70px 15px;
}
.activity .acBox {
  background: #ebebeb;
  padding: 15px;
  margin: 10px 0;
  border-radius: 3px;
}
.activity .acBox img {
  height: 150px;
}
.activity .acBox .ac-title {
  color: #cc1138;
  font-size: 13px;
  padding: 10px 0;
}
.activity .acBox .a-content {
  font-size: 12px;
  line-height: 1.4;
}
.activity .acBox .ac-price {
  color: #cc1138;
  text-align: right;
  font-size: 16px;
  margin: 10px 5px 0 0;
}


