
.registerTeacher {
  padding: 10px 10px;
}
.registerTeacher ul li {
  height: 100%;
  border: solid 1px #ebebeb;
  color: #000;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 10px 0;
  border-radius: 5px;
}
.registerTeacher ul li label {
  display: inline-block;
  width: 70px;
  padding: 10px 5px;
  line-height: 1.3;
  text-align: center;
  font-size: 14px;
  border-right: solid 1px #ebebeb;
}
.registerTeacher ul li select {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 10px 10px;
  outline: none;
  border: none;
  background: #fff;
}
.registerTeacher ul li input {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  padding: 10px 10px;
  outline: none;
  border: none;
  background: #fff;
}
.registerTeacher .reg-sub {
  margin: 20px 0;
}
.registerTeacher .reg-sub input {
  font-size: 16px;
}
.registerTeacher .reg-submit input {
  width: 100%;
  background: #cc1438;
  border: none;
  outline: none;
  border-radius: 3px;
  font-size: 18px;
  color: #ebebeb;
  padding: 12px 0;
}


