
.activityDetail {
  height: 100%;
  padding-bottom: 70px;
}
.activityDetail .ac-title {
  font-size: 14px;
  color: #cc1138;
  padding-bottom: 10px;
}
.activityDetail .ac-dBox {
  height: 180px;
  margin-bottom: 8px;
}
.activityDetail .ac-dBox img {
  height: 100%;
}
.activityDetail .ac-course {
  padding: 0 15px;
}
.activityDetail .ac-course .ac-content {
  padding: 15px 0 15px 0;
}
.activityDetail .ac-course .ac-content ul li {
  font-size: 12px;
  line-height: 2;
  color: #333;
}
.activityDetail .ac-registration {
  background: #ebebeb;
  padding: 10px 15px;
}
.activityDetail .ac-registration .ac-uesr {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.activityDetail .ac-registration .ac-uesr ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.activityDetail .ac-registration .ac-uesr ul li {
  width: 25%;
  text-align: center;
}
.activityDetail .ac-registration .ac-uesr ul li img {
  width: 43px;
}
.activityDetail .ac-registration .ac-uesr ul li div {
  font-size: 12px;
  padding: 5px 0;
}
.activityDetail .ac-registration .ac-uesr .ac-text {
  font-size: 12px;
  margin: 15px 0 0 10px;
}
.activityDetail .ac-registration .ac-uesr .ac-text span {
  vertical-align: middle;
  display: inline-block;
  margin-right: 5px;
}
.activityDetail .ac-registration .ac-uesr .ac-text .ac-right {
  height: 15px;
  width: 9.2px;
  vertical-align: middle;
}
.activityDetail .ac-submit {
  padding: 15px 15px;
}
.activityDetail .ac-submit input {
  width: 100%;
  background: #cc1438;
  border: none;
  outline: none;
  border-radius: 3px;
  font-size: 18px;
  color: #ebebeb;
  padding: 12px 0;
}


